#jumbo { 
    position: relative;
    img {
        object-fit: cover;
        height: 100%;
    }
    .overlay {
        text-align: center;
        position: absolute;
        width: 100%;
        height: 100%;
        background: 
        linear-gradient(to right, rgba(0,0,0,1) 0%, rgba(0,0,0,0.7) 50%, rgba(0,0,0,0.4) 100%);
        display: flex;
        align-items: center;
        .jumbo-content {
            color: white;
            padding: 0 15px;
            margin: auto;
            h2 {
                font-weight: 400;
                text-transform: uppercase;
                line-height: 3.3rem;
                margin-bottom: 25px;
                letter-spacing: 0.4px;
            }
            p {
                font-size: 1.25rem;
                line-height: 2.25rem;
                margin-bottom: 35px;
                letter-spacing: 0.4px;
            }
            .button {
                background-color: #67809f;
                border-radius: 3px;
                border: 1px solid transparent;
                color: white;
                display: inline-block;
                line-height: 50px;
                text-transform:uppercase;
                height: auto;
                padding: 5px 20px;
                font-size: 1.26rem;
                font-weight: 400;
                letter-spacing: 0.4px;
                text-align: center;
                vertical-align: middle;
                height:auto;
                font-size: 1.25rem;
                text-size-adjust: 100%;
                .fa-regular {
                    margin-left: 12px;
                    margin-right: -4px;
                    font-size: 1.25rem;
                    font-weight: 600;
                    line-height: 50px;
                }
            }
        }
        .dots {
            position: absolute;
            bottom: 51px;
            li {
                width: 11px;
                height: 11px;
                background-color: #ffffff;
                opacity: 0.5;
                border-radius: 30px;
                margin: 0 10px;
            }
            li.active {
                outline: 2px solid white;
                outline-offset: 5px;
                background-color: white;
                opacity: 1;
            }
        }
    }
}